@charset "utf-8";

@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

body {
    height: 100vh;
    background: url(../images/bg.jpg) no-repeat center / cover fixed;
    font-family: 'Pretendard-Regular';

}

.container {
    padding: 50px 0;
}

.container .heros {
    display: flex; flex-wrap: wrap;
    justify-content: center;
    max-width: 660px;
    margin: 0 auto;
    padding: 40px 20px;
     .heros .hero {
        width: 84px;
        height: 84px;
        margin: 4px;
        border: 3px solid #fff;
        border-radius: 10px;
        box-sizing: border-box;
        overflow: hidden;
        background-color: #555;
        transform: skew(-14deg);
        transition: transform .1s, background-color .6s;
    }
    
}

.container .heros .hero:hover {
    background-color: #ff9c00;
    transform: skewX(-14deg) scale(1.4);
    z-index: 1;
}

.container .heros .hero .image {
    width: 140%; height: 100%;
    transform: translateX(-16px) skew(14deg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 90px;
}

